import React, { Component } from 'react';
import PropTypes from 'prop-types'

class SymbolsList extends Component {
  render() {
    const { values } = this.props;
    let list;

    if(values) {
      list = values.map(item =>
        <tr key={item.symbol}><td> {item.symbol} </td><td> {item.ask} </td>
        <td>  {item.bid} </td><td> {item.description} </td></tr>
      )
    } else {
      list = <tr><td>Instruments not available</td></tr>
    }

    return (
      <div className="row">
        <div className="col-md-4 col-md-offset-4">
          <h3>Available instruments</h3>
          <table className="table">
            <thead>
              <tr>
                <th>Symbol</th>
                <th>Ask</th>
                <th>Bid</th>
                <th>Description</th>
              </tr>
            </thead>
            <tbody>
              {list}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

SymbolsList.defaultProps = {
  values: [],
}

SymbolsList.propTypes  = {
  values: PropTypes.array,
}

export default SymbolsList;











