import React, { Component } from 'react';
import PropTypes from 'prop-types'

class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      userId: process.env.REACT_APP_DEFAULT_XTB_USER || "",
      password: process.env.REACT_APP_DEFAULT_XTB_PASSWORD || "",
    };
  }

  onNameChange = (e) => {
    this.setState({userId: e.target.value});
  }

  onPasswordChange = (e) => {
    this.setState({password: e.target.value});
  }

  onSubmit = (e) => {
    e.preventDefault();
    this.props.onLogin(this.state.userId, this.state.password);
    this.setState({userId: "", password: ""});
  }

  render() {
    return (
      <div className="row">
        <div className="col-md-4 col-md-offset-4">
          <h1>Forex xAPI WebApp</h1>
          <form onSubmit={this.onSubmit} >
            <div className="form-group">
              <label>User id</label>
              <input type="text" className="form-control" 
                placeholder="User ID" 
                value={this.state.userId} 
                onChange={this.onNameChange}  
                />
            </div>
            <div className="form-group">
              <label >Password</label>
              <input type="password" className="form-control" 
              placeholder="Password"
              value={this.state.password} 
              onChange={this.onPasswordChange} 
              />
            </div>
            <button type="submit" className="btn btn-primary">Log in</button>
          </form>
        </div>
      </div>
    );
  }
}



Login.propTypes  = {
  onLogin: PropTypes.func,
}

export default Login;




