import { take, call, put, all, fork, takeEvery, takeLatest, select } from "redux-saga/effects";
import { eventChannel, delay } from 'redux-saga'

import * as types from '../constants/ActionTypes'

// import {setupSocket} from './sockets'

const WATCHDOG_TIME = 15000;

function createWebSocketConnection() {
  const url = "wss://ws.xapi.pro/demo";
  console.log('Connecting to: ' + url);
  return new WebSocket(url);
}

function login(userId, password, ws) {
  let msg = {};
  msg.command = "login";
  let params = {};
  params.userId = parseInt(userId, 10);
  params.password = password;
  msg.arguments = params;
  console.log('Trying to log in as: ' + msg.arguments.userId);
  send(msg, ws );
} 

function send(jsonMessage, socket) {
  try {
    let msg = JSON.stringify(jsonMessage);
    socket.send(msg);
    console.log('Sent ' + msg.length + ' bytes of data: ' + msg);
  } catch(Exception) {
    console.error('Error while sending data: ' + Exception.message);
  }
}

// this function creates an event channel from a given socket
// Setup subscription to incoming `ping` events
function createSocketChannel(socket, action){
  // `eventChannel` takes a subscriber function
  // the subscriber function takes an `emit` argument to put messages onto the channel
  return eventChannel(emit => {

    const messageHandler = (event) => {
      // puts event payload into the channel
      // this allows a Saga to take this payload from the returned channel
      console.log("Received: " + event.data);

      try {
        let response = JSON.parse(event.data);
        emit({"socketResponse": response});
        
      } catch (Exception) {
        alert('Fatal error while receiving data! :(');
      }
    }

    socket.onopen = () => {
      console.log('Connected');
      emit({"socketStatus": "Connected"})
      login(action.user , action.password, socket);
    }
    
    // setup the subscription
    socket.onmessage = (event) => messageHandler(event);

    socket.onerror = (event) => {
      console.log(event)
      emit({"socketStatus": "Connection error"})
    }

    socket.onclose = () => {
      emit({"socketStatus": "Disconnected"})
      console.log('Connection closed');
    };

    // the subscriber must return an unsubscribe function
    // this will be invoked when the saga calls `channel.close` method
    const unsubscribe = () => {
      socket.off('message', messageHandler)
      console.log("cancelling subscription")
    }

    return unsubscribe
  })
}

function pingSocket( socket) {
  let msg = {};
  msg.command = "ping";
  send(msg, socket );
} 

function disconnect(socket) {
  // console.log("Logout request")
  socket.close();
}

function* resServerTimeRequest() {
    yield take(types.MESSAGE_RECEIVED)
    let msg = yield select(state => state.get("messages").get("receivedData").returnData)
    yield put({ type: types.RES_SERVER_TIME_REQUEST, msg })
}

function* getServerTimeReq(socket) {
  let msg = {};
  msg.command = "getServerTime";
  yield fork(resServerTimeRequest)
  yield call(send, msg, socket );
}

function* receivePing() {
  yield take(types.MESSAGE_RECEIVED)
  let msg = yield select(state => state.get("messages").get("receivedData").status)
  yield put({ type: types.RES_PING, msg })
}

function* ping(socket) {
  let pingTask
  do {
    yield call(delay, WATCHDOG_TIME)
    if(pingTask && pingTask.isRunning()) {
      let msg = false;
      yield put({ type: types.RES_PING, msg })
      pingTask.cancel()  
    }
    pingTask = yield fork(receivePing)
    yield call(pingSocket, socket)
  } while(socket.readyState === 1 )
}

function* handleLogout(socket) {
  yield takeEvery(types.LOGOUT_REQUEST, () => disconnect(socket));  
}

function* handleGetServerTime(socket) {
  while(true) {
    yield take(types.GET_SERVER_TIME_REQUEST);
    yield fork(getServerTimeReq,socket);
  }
}

export function* handleLogin(action) {
  const socket = yield call(createWebSocketConnection)
  const socketChannel = yield call(createSocketChannel, socket, action)

  yield fork(handleGetServerTime, socket)
  yield fork(ping, socket)
  yield fork(handleLogout, socket)

  while (true) {
    const channelMsg = yield take(socketChannel)
    if(channelMsg.socketResponse) {
      const response = channelMsg.socketResponse;
      if(response.status === true) {
        if(response.streamSessionId !== undefined) {
          // We received login response        
          yield put({ type: types.LOGIN_SUCCESS, response })
          //this.getAllSymbols();

          //any other response
        } else {
          yield put({ type: types.MESSAGE_RECEIVED, response })
          // We received getAllSymbols response
          // this.parseGetAllSymbols(response.returnData);
        }
      } else {
        alert('Error: ' + response.errorDescr);
        yield put({ type: types.RESPONSE_ERR, response })
        let loginPending = yield select(state => state.get("messages").get("loginPending"))
        if(loginPending && (response.errorCode === "BE005" || "EX000" || "EX007")) {
          yield put({ type: types.LOGIN_FAILURE, response })
        }
      }
    }
    if(channelMsg.socketStatus) {
      console.log(channelMsg)
      yield put({ type: types.SOCKET_STATUS, channelMsg })
      if(channelMsg.socketStatus==="Disconnected") {
        yield put({ type: types.LOGOUT_SUCCESS })
      }
    }   
  }
}

function* loginSaga() {
  yield takeLatest(types.LOGIN_REQUEST, (action) => handleLogin(action));
}

export default function* rootSaga() {
  yield all([
    fork(loginSaga),
  ])
}



