import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
// import { createStructuredSelector } from 'reselect';

import { loginRequest, logoutRequest, getServerTime  } from '../actions'
import Login from '../components/Login'
import SymbolsList from '../components/SymbolsList'
// import { makeSelectSessionId } from './selectors'

export class HomePage extends Component {

  render() {
    return (
      <div>
        <Login onLogin={this.props.loginRequest} />
        <br />
        <button onClick={this.props.logoutRequest}> LOGOUT </button>
        <br />
        <button onClick={this.props.getServerTime}> GET Server Time </button>
        <br />
        Login Sessoin Id: {this.props.messages.streamSessionId}
        <br />
        SOCKET STATUS: {this.props.messages.socketStatus}
        <br />
        <SymbolsList values={this.props.messages.values} />
      </div>
    );
  }
}

HomePage.defaultProps = {
  messages: {}
}

HomePage.propTypes = {
  // Injected by React Redux
  messages: PropTypes.object,
  location: PropTypes.object.isRequired,
}

const mapStateToProps = (state) => ({
  messages: state.get('messages').toObject(), 
})

export default withRouter(connect(mapStateToProps, {
  loginRequest,
  logoutRequest,
  getServerTime,
})(HomePage))

// TOBE used at later for example for extracting specific stock data
