// SEND_MESSAGE = 'SEND_MESSAGE'
// MESSAGE_RECEIVED = 'MESSAGE_RECEIVED'
// LOGIN_REQUEST = 'LOGIN_REQUEST'
// GET_ALL_SYMBOLS_REQUEST = 'GET_ALL_SYMBOLS_REQUEST'
// LOGIN_RESPONSE = 'LOGIN_RESPONSE'
import { fromJS } from 'immutable';

const initialState = fromJS({
  userId: null,
  loginPending: false,
  loggedId: false,
  receivedData: null, // object or array of objects depending on the query
  streamSessionId: null,
  errorMessage: null,
  // keepAlive: null,
  balance: null,
  socketStatus: "Disconnected",
  pingResponse: false,
  serverTime: null,
})

const messages = (state=initialState, action) => {
  switch (action.type) {
    // case 'SEND_MESSAGE':
    //   return {
    //     ...state,
    //     //TODO..........
    //   }

    case 'MESSAGE_RECEIVED':
      return state
        .set("receivedData", action.response)
        .set("errorMessage", null)

    case 'SOCKET_STATUS':
      return state
        .set("socketStatus", action.channelMsg.socketStatus)

    case 'LOGIN_REQUEST':
      return state
        .set("userId", action.user)
        .set("loginPending", true)

    case 'LOGIN_SUCCESS':
      return state
        .set("streamSessionId",action.response.streamSessionId)
        .set("loginPending",false)
        .set("loggedId",true)

    case 'LOGIN_FAILURE':
      return state
        .set("loginPending",false)
    
    case 'LOGOUT_SUCCESS':
      return state
        .set("loggedId", false)

    case 'RES_SERVER_TIME_REQUEST':
      return state
        .set("serverTime", action.msg)

    case 'RESPONSE_ERR':
      return state
        .set("errorMessage", action.response.errorDescr)

    case 'RES_PING':
      return state
        .set("pingResponse", action.msg)
        
    
    default:
      return state
  }
}

export default messages



